<template>
	<view class="content">
		<view class="search-wrap wrap-content">
			<image class="logo-img" src="/static/img/logo.png"></image>
			<view class="input">
				<input v-model="searchVal" type="text" confirm-type="search" @confirm="search" focus>
				<!-- <view class="search-logo" @click="handleSearch">
					<view class="logo"></view>
					<text>搜索</text>
				</view> -->
			</view>
		</view>
		<view class="full-column">
			<view class="title">
				<text>搜索记录</text>
				<image src="/static/img/delete.png" @click="clear"></image>
			</view>
			<view class="search-history-list">
				<text class="search-history-item" v-for="(item, i) in history" :key="i" @click="itemClick(item)">{{item}}</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default{
		data() {
			return {
				searchVal: '',
				history: []
			}
		},
		created() {
			uni.getStorage({
				key: 'search_keys',
				success: res => {
					this.history = JSON.parse(res.data)
				}
			});
		},
		methods:{
			itemClick(key) {
				this.searchVal = key
				this.search()
			},
			search() {
				if (this.searchVal === '') return
				if(!this.history.includes(this.searchVal)) {
					this.history.push(this.searchVal)
					uni.setStorage({
						key: 'search_keys',
						data: JSON.stringify(this.history)
					});
				}
				uni.showToast({
					icon: 'none',
					title: '搜索结果为空'
				})
			},
			clear() {
				uni.removeStorage({
					key: 'search_keys',
					success: () => {
						this.history = []
					}
				});
			}
		}
	}
</script>

<style lang="scss" scoped>
	.search-wrap{
		flex: 0 0 112rpx;
		padding: 20rpx 30rpx;
		box-sizing: border-box;
		display: flex;
		align-items: center;
		.input{
			flex: 1;
			position: relative;
			margin-left: 24rpx;
			font-size: 26rpx;
			.search-logo{
				width: 100%;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				display: flex;
				align-items: center;
				justify-content: center;
				pointer-events: none;
				text{
					color: #BBBBBB;
					font-weight: blod;
				}
				.logo{
					width: 25rpx;
					height: 25rpx;
					margin-right: 11.5rpx;
					background: url(data:image/png;base64,) no-repeat center center / 24rpx auto;
				}
			}
			>input{
				height: 72rpx;
				line-height: 72rpx;
				background-color: #f5f5f5;
				border-radius: 8rpx;
				padding: 0 20rpx;
				&:hover + .search-logo{
					display: none;
				}
			}
		}
		.logo-img{
			flex: 0 0 182rpx;
			width: 182rpx;
			height: 44rpx;
		}
	}
	.full-column{
		.title{
			background-color: #fff;
			font-size: 28rpx;
			margin-top: 15rpx;
			line-height: 72rpx;
			padding: 0 20rpx;
			display: flex;
			justify-content: space-between;
			align-items: center;
			>image{
				width: 32rpx;
				height: 32rpx;
			}
		}
		.search-history-list{
			display: flex;
			flex-wrap: wrap;
			padding: 20rpx;
			.search-history-item{
				margin-right: 15rpx;
				margin-bottom: 15rpx;
				padding: 10rpx 15rpx;
				line-height: 52rpx;
				background-color: #fff;
				border-radius: 8rpx;
				font-size: 26rpx;
				color: #666;
				white-space: nowrap;
			}
		}
	}
</style>
